.green-line {
  position: relative; }

.green-line__wrapper {
  display: grid;
  grid-template-columns: min-content auto; }

.green-line__header {
  margin: 0;
  padding: 0;
  display: inline-block;
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0;
  color: black;
  padding: 25px 20px 32px 0;
  white-space: nowrap;
  background-color: white;
  z-index: 2; }
  @media (min-width: 768px) {
    .green-line__header {
      font-size: 36px;
      line-height: 36px;
      padding: 65px 38px 80px 38px; } }
  @media (min-width: 1440px) {
    .green-line__header {
      left: -40px;
      padding: 78px 38px 80px 38px; } }

.green-line__header--comment {
  padding: 25px 20px 37px 0; }
  @media (min-width: 768px) {
    .green-line__header--comment {
      padding: 66px 44px 75px 40px; } }
  @media (min-width: 1440px) {
    .green-line__header--comment {
      padding: 68px 44px 75px 40px; } }

.green-line__header--additional {
  padding: 22px 20px 34px 0; }
  @media (min-width: 768px) {
    .green-line__header--additional {
      padding: 65px 44px 56px 40px; } }
  @media (min-width: 1440px) {
    .green-line__header--additional {
      padding: 68px 44px 56px 40px; } }

@media (min-width: 768px) {
  .green-line__header--contacts {
    padding: 77px 44px 75px 40px; }
    .green-line__header--contacts br {
      display: none; }
    .green-line__header--contacts span {
      display: inline-block;
      padding-left: 7px; } }

@media (min-width: 1440px) {
  .green-line__header--contacts {
    padding: 68px 44px 75px 40px; } }

.green-line__line {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "empty greeny";
  z-index: 1; }
  @media (min-width: 1440px) {
    .green-line__line {
      bottom: 48%; } }
  .green-line__line::before {
    content: "";
    grid-area: empty;
    height: 2px;
    background-color: white; }
    @media (min-width: 768px) {
      .green-line__line::before {
        background-color: #68b738; } }
  .green-line__line::after {
    content: "";
    grid-area: greeny;
    height: 2px;
    background-color: #68b738; }

.green-line__line--contacts {
  bottom: 62%; }
  @media (min-width: 768px) {
    .green-line__line--contacts {
      bottom: 47%; } }

@media (min-width: 768px) {
  .green-line__line--additional {
    bottom: 43%; } }

