.order-all {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: url("../img/decorative/gift-mobile.jpg") center no-repeat;
  background-size: contain;
  background-color: $basic-green;
  padding: 58px 61px 27px 58px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/decorative/gift-mobile@2x.jpg");
    background-size: contain;
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding: 58px 124px 30px 80px;
    background: url("../img/decorative/gift-tablet.jpg") center no-repeat;
    background-color: $basic-green;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/decorative/gift-tablet@2x.jpg");
      background-size: contain;
    }
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    align-items: center;
    padding: 56px 43px 28px 41px;
    background: url("../img/decorative/gift-desktop.jpg") center no-repeat;
    background-color: $basic-green;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/decorative/gift-desktop@2x.jpg");
      background-size: contain;
    }
  }
}

.order-all__icon {
  width: 78px;
  height: 80px;
  background: url("../img/decorative/gift.svg") center no-repeat;
  margin-bottom: 32px;
}

.order-all__text {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  color: $basic-background;

  @media (min-width: $tablet-width) {
    width: 162px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 162px;
    text-align: center;
  }
}
